.c-icon-blocks {
  background: $s-color--background-subdued;
  transform: translateY(1px);

  &__inner {
    padding: 2.9375rem 0.9375rem;
    max-width: 31.25rem;
    margin: 0 auto;
  }

  &__title {
    text-align: center;
  }
 
  @include t-media-query($s-small-up) {
    &__inner {
      padding: 2.9375rem 2.75rem;
      max-width: initial;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding: 6.25rem 4rem;
    }    
  }

  @include t-media-query($s-large-up) {
    &__inner {
      padding: 6.25rem 0 10.625rem;
      max-width: 72.5rem;
    }
  }
}
